import React from "react";
import { withTranslation } from "react-i18next";
import { inject, observer } from "mobx-react";
import AutoSizer from "react-virtualized-auto-sizer";
import { FixedSizeGrid as Grid } from "react-window";
import { observable } from "mobx";
import "./style.scss";
import { storage } from "../../util/util";
import Loading from "../../common/Widget/Loading";
import Nodata from "../../common/Widget/Nodata";
import Checkbox from "../../common/Widget/Checkbox";
import DeleteModal from "../../common/Widget/DeleteModal";
// import Chooseframe from "../../common/Widget/Chooseframe";
import event from "../../util/event";
import NewBtn from "../../common/NewBtn";
import Operation from "../../common/Operation";
import ListCell from "../../common/ListCell";

//ls变量名称
const PLAYLISTSORT = "PlaylistSort";
const PLAYLISTSORT_1_ISDES = "PlaylistSort_isdes";

@withTranslation()
@inject("playlist", "rahmen", "login", "uploadFiles")
@observer
class Playlist extends React.Component {
  constructor(props) {
    super(props);
    this.t = this.props.t;
    this.boxRef = React.createRef();
  }

  @observable menuDisplayId = null;
  @observable rename = false;
  @observable renameid = null;
  @observable ren_defaultValue = "";
  checkAll = false;
  @observable sortList = [
    { id: 1, text: "updateSort" },
    { id: 2, text: "creationSort" },
    { id: 3, text: "photosSort" },
    { id: 4, text: "nameSort" },
  ];
  @observable defaultSort = {};
  @observable sortindex = 0;
  @observable isdes = false;
  @observable del_visible = false;
  @observable a_deleteId = null;
  // @observable set_visible = false;
  // @observable setDataList = false;
  // @observable ckick_listId = null;
  @observable sortDis = false;
  @observable childW = 0;
  @observable childH = 0;
  @observable columnCount = 0;
  @observable rowCount = 0;
  @observable morelist = [
    { icon: "edit", name: this.props.t("rename") },
    { icon: "delete", name: this.props.t("delete") },
  ];
  @observable listRowNum = 0;

  componentDidMount() {
    let obj = {};
    const idx = storage.get(PLAYLISTSORT);
    const order = storage.get(PLAYLISTSORT_1_ISDES);
    if (!idx && idx !== 0) {
      storage.set(PLAYLISTSORT, 0);
      obj = this.sortList[0];
    } else {
      obj = this.sortList[idx];
      this.sortindex = idx;
    }
    this.defaultSort = obj;
    this.isdes = order || false;

    if (this.props.playlist.firstInit) {
      this.resize();
    }

    // this.props.playlist.playlist_init(
    //   storage.get(PLAYLISTSORT),
    //   this.isdes,
    //   this.boxRef.current.offsetWidth
    // );
    this.props.playlist.playlist_init(
      storage.get(PLAYLISTSORT),
      this.isdes,
      this.boxRef.current.offsetWidth,
      (obj) => {
        const { wi, hi, colNum, rowNum } = obj;
        this.childW = wi;
        this.childH = hi;
        this.columnCount = colNum;
        this.listRowNum = rowNum;
      },
      this.RowNum
    );

    document.addEventListener("click", this.hide, true);
    document.addEventListener("click", this.sorthide);

    window.addEventListener("resize", this.resize);

    this.addPlaylist = event.on("addPlaylist", () => {
      this.checkAll = false;
    });

    this.up_refresh = event.on("up_refresh", this.refresh);

    this.wsinit = event.on("wsinit", (res) => {
      if (res === 1) {
        this.refresh();
      }
    });
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.hide, true);
    document.removeEventListener("click", this.sorthide);
    window.removeEventListener("resize", this.resize);
    event.rm(this.wsinit);
    event.rm(this.up_refresh);
    event.rm(this.addPlaylist);
    this.props.playlist.replacement();
  }

  resize = () => {
    const { wi, hi, colNum, rowNum } = this.props.playlist.utils(
      this.boxRef.current.offsetWidth
    );
    this.childW = wi;
    this.childH = hi;
    this.columnCount = colNum;
    this.listRowNum = rowNum;
  };

  RowNum = (num) => {
    this.listRowNum = num;
  };

  getWH = () => {
    this.props.playlist.resize(
      this.boxRef.current.offsetWidth,
      this.props.playlist.playList
    );
  };

  refresh = () => {
    this.props.playlist.playlist_init(
      storage.get(PLAYLISTSORT),
      this.isdes,
      this.boxRef.current.offsetWidth,
      (obj) => {
        const { wi, hi, colNum, rowNum } = obj;
        this.childW = wi;
        this.childH = hi;
        this.columnCount = colNum;
        this.listRowNum = rowNum;
      },
      this.RowNum
    );
  };

  // 更多modal隐藏
  hide = (e) => {
    if (e.target && e.target.nodeName === "svg") {
      return;
    }
    this.menuDisplayId = null;
  };
  // 排序modal隐藏
  sorthide = (e) => {
    if (e.target && e.target.nodeName === "span") {
      return;
    }
    this.sortDis = false;
  };

  //更多modal显示
  more_show = (e, node) => {
    e.stopPropagation();
    if (this.menuDisplayId) {
      this.menuDisplayId = null;
    } else {
      this.menuDisplayId = node;
      this.sortDis = false;
    }
  };

  //重命名列表start
  renamefn = (id, name) => {
    this.rename = true;
    this.renameid = id;
    this.ren_defaultValue = name;
  };
  //重命名列表确定
  ren_handleconfirm = (name) => {
    const { renameid } = this;
    this.props.playlist.playlist_rename(renameid, name);
    this.rename = false;
    this.renameid = null;
  };
  ren_handlecancel = () => {
    this.rename = false;
    this.renameid = null;
  };
  //重命名列表end

  //删除列表start
  delete(id) {
    this.del_visible = true;
    this.a_deleteId = [id];
  }
  del_handleconfirm = () => {
    const { a_deleteId } = this;
    this.del_visible = false;
    this.checkAll = false;
    this.a_deleteId = null;
    this.props.playlist.playlist_delete(a_deleteId);
  };
  del_handlecancel = () => {
    this.del_visible = false;
    this.a_deleteId = null;
  };
  //删除列表end

  //排序start
  sortEldis = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    this.menuDisplayId = null;
    this.sortDis = !this.sortDis;
  };
  changeIndex(i) {
    storage.set(PLAYLISTSORT, i);
    if (this.sortindex === i) {
      this.isdes = !this.isdes;
      storage.set(PLAYLISTSORT_1_ISDES, this.isdes);
    } else {
      this.sortindex = i;
      storage.set(PLAYLISTSORT_1_ISDES, this.isdes);
    }
    this.defaultSort = this.sortList[i];
    this.props.playlist.playlist_sort1(i, this.isdes);
  }
  //排序end

  //全勾选/选择start
  checkAllfn = () => {
    this.checkAll = !this.checkAll;
    this.props.playlist.checkAllplaylist(this.checkAll);
  };
  //勾选/选择end

  // //绑定的相框
  // chooseframe(e, id) {
  //   e.stopPropagation();
  //   this.set_visible = true;
  //   this.setDataList = true;
  //   this.ckick_listId = id;
  //   this.props.playlist.playlist_detail_frames(id, () => {
  //     this.setDataList = false;
  //   });
  //   event.on("framesNum", () => {
  //     this.props.playlist.playlist_detail_frames(id, () => {
  //       this.props.playlist.playlist_init();
  //     });
  //   });
  // }

  // //相框操作隐藏
  // framesHide = () => {
  //   event.rmName("framesNum");
  //   this.set_visible = false;
  // };

  // //同步相框播放确定
  // frameonPlay = (node) => {
  //   this.props.playlist.frameonPlay(this.ckick_listId, node);
  // };

  //跳转列表详情
  goPlaylistdetails = ({ id, name, created, frame_count }) => {
    this.props.playlist.goUrl(name, created, frame_count);
    this.props.history.push(`/playlist/detail/${id}`);
  };

  pitchOn = (e, id, frame_default) => {
    this.props.playlist.pitchOn(
      e,
      id,
      frame_default,
      () => {
        this.checkAll = true;
      },
      () => {
        this.checkAll = false;
      }
    );
  };

  click = (val, item) => {
    val.domEvent.stopPropagation();
    if (val.key === "0") {
      this.renamefn(item.id, item.name);
    } else if (val.key === "1") {
      this.delete(item.id);
    }
  };

  acceptedFiles = (item, files) => {
    this.props.uploadFiles.upFile(files, item.id, null);
  };

  render() {
    const { t } = this.props;
    // const { OS } = this.props.login;
    const {
      rename,
      ren_defaultValue,
      sortList,
      defaultSort,
      del_visible,
      // set_visible,
      isdes,
      sortDis,
      // setDataList,
      // ckick_listId,
      sortindex,
    } = this;
    const {
      playList,
      removeplayList,
      // frameList,
      createPlaylist,
      // frame_list,
      firstInit,
      invert,
      cancelAll,
    } = this.props.playlist;
    const time =
      sortindex === 1 ? `${t("CreationTime")}：` : `${t("LastUpdate")}：`;

    return (
      <div className="playlist">
        <NewBtn
          icon={"icon-liebiao"}
          created={createPlaylist}
          btnText={t("NewPlaylist")}
          num={playList.filter((el) => el.id).length}
          name={t("playlist")}
        />
        <Operation
          selectNum={removeplayList.length}
          dataNum={playList.filter((el) => el.id).length}
          oncheck={this.checkAllfn}
          invert={invert}
          remove={() => {
            this.del_visible = true;
          }}
          sort={true}
          sortList={sortList}
          defaultSort={defaultSort}
          isdes={isdes}
          sortDis={sortDis}
          sortEldis={this.sortEldis}
          changeIndex={(i) => {
            this.changeIndex(i);
          }}
          cancelAll={cancelAll}
        />

        <div className="playlist-main" ref={this.boxRef}>
          {firstInit ? (
            playList.length > 0 ? (
              <AutoSizer>
                {({ height, width }) => {
                  // this.adaption()
                  // this.listRowNum = playList.length / this.columnCount;
                  return (
                    <Grid
                      className="Grid"
                      itemData={playList}
                      rowCount={this.listRowNum}
                      columnCount={this.columnCount}
                      columnWidth={this.childW}
                      height={height}
                      rowHeight={this.childH}
                      width={width}
                    >
                      {({ columnIndex, data, rowIndex, style }) => {
                        const index = rowIndex * this.columnCount + columnIndex;
                        const item = data[index];
                        return (
                          <ListCell
                            // key={item.id}
                            item={item}
                            name={item && item.name}
                            timeName={time}
                            sortindex={sortindex}
                            style={style}
                            oncheck={this.pitchOn}
                            morelist={this.morelist}
                            selectNum={removeplayList.length}
                            click={this.click}
                            goPlaylistdetails={this.goPlaylistdetails}
                            title={t("UploadToList")}
                            acceptedFiles={this.acceptedFiles}
                          />
                        );
                      }}
                    </Grid>
                  );
                }}
              </AutoSizer>
            ) : (
              <Nodata />
            )
          ) : (
            <Loading />
          )}
        </div>

        {/* {set_visible && (
          <Chooseframe
            setDataList={setDataList}
            frameList={frameList}
            frame_list={frame_list}
            ckick_listId={ckick_listId}
            framePlay={this.frameonPlay}
            handlecancel={this.framesHide}
          />
        )} */}

        {rename ? (
          <Checkbox
            title={t("rename")}
            defaultValue={ren_defaultValue}
            handlecancel={this.ren_handlecancel}
            handleconfirm={this.ren_handleconfirm}
          />
        ) : null}

        {del_visible ? (
          <DeleteModal
            title={t("Deleteconfirm")}
            text={t("list")}
            custom={t("Deletedefault")}
            handlecancel={this.del_handlecancel}
            handleconfirm={this.del_handleconfirm}
          />
        ) : null}
      </div>
    );
  }
}

export default Playlist;
