import React, { Component } from 'react';
import { Icon } from 'antd';
import { withTranslation } from 'react-i18next';
import './style.scss'

@withTranslation()
class DeleteModal extends Component {
  constructor(props) {
    super(props);
    this.inputRef = React.createRef();
  }
  render() {
    const { t, title, text, custom, handlecancel, handleconfirm } = this.props;
    return (
      <div className='delete-modal'>
        <div className='modal-warp'>
          <div className='modal-warp-header'>
            <h3>{title}</h3>
            <span onClick={handlecancel}>
              <Icon type="close" />
            </span>
          </div>
          <div className='modal-warp-boby'>
            <div className='modal-warp-text'>
              <p className='subtitle'>{t('deleteTheSelected')}{text}？</p>
              <p className='warning'>{t('modalWarning')}{text}{t('CarefulOperation')}</p>
              <p className='warning'>
                {custom && custom}
              </p>
            </div>
            <div className='modal-warp-handle'>
              <span className='cancel-btn' onClick={handlecancel}>{t('Cancel')}</span>
              <span
                className='confirm-btn'
                onClick={handleconfirm}>{t('Determine')}</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default DeleteModal;