import React, { PureComponent } from 'react';
import './index.scss';

export default class NewBtn extends PureComponent {

  render() {
    const { created, num, btnText, name, icon } = this.props;
    return (
      <div className='new_btn_inner'>
        <div className='new_btn'>
          <button onClick={created}>{btnText}</button>
        </div>
        <div className='new_btn_right'>
          <div className='new_Icon'><i className={`iconfont ${icon}`}></i></div>
          <div className='num-text'>
            <p className='text'>{name}</p>
            <p className='num'>{num || 0}</p>
          </div>
        </div>
      </div>
    );
  }
}